
.body {
	box-sizing: border-box;
	margin:0;
	padding:0;
	font-family: 'Montserrat', sans-serif;
}

.overflow {
	overflow-x: hidden;
	overflow-y: hidden;
	display: flex;
	flex-direction: column;
	justify-content: center;
}

.list {
	list-style-type: none;
}

.select {
	display: flex;
	flex-direction: column;
}
