//form main__form

.main__form {
	margin: 20px;
	padding: 20px;
	border: 1px solid #022F40;
	@media all and (min-width: 481px){
		display: flex;
		justify-content: center;
		flex-direction: column;
		margin-left: 25%;
		width: 50%;
	}
}

.feedback__title{
	@media all and (min-width: 481px){
		margin-bottom: 20px;
	}
}

.main__form__fields {
	display: flex;
	flex-direction: column;
}

.main__form__options{
	display: flex;
}

.main__form__textarea{
	margin-top: 2px;
}

.main__form__button--send{
	width: 25%;
	height: 30px;
	box-shadow: 0 5px 2px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);
	border-radius: 5px;
}

.main__form__button--backhome {
	display: block;
	text-decoration: none;
	color: white;

	&:hover {
		background-color: #C1EDCC;
		color: $primary-color;
		border-radius: 10px;
		padding: 25px;
	}
}

.main__form__thanks {
	display: none;
	position: fixed;
	top: 0;
	left: 0;
	background-color: #599675;
	color: white;
	width: 100vw;
	height: 100vh;
	z-index: 1;
	transition: all .3s;
	text-align: center;
	padding-top: 100px;
	@media all and (min-width: 769px){

		display: none;
		position: fixed;
		top: 0;
		left: 0;
		background-color: #599675;
		color: white;
		width: 100vw;
		height: 100vh;
		z-index: 1;
		transition: all .3s;
		text-align: center;
		padding-top: 100px;

	}
}

.show{
	display: block;
	@media all and (min-width: 769px){
		display: block;
	}

}

.show h2{
	font-size: 34px;
	margin-top: 34px;
	@media all and (min-width: 769px){
		font-size: 57px;
	}
}

.feedback__thanks.show p {
	font-size: 20px;
	margin-top: 20px;
	@media all and (min-width: 769px){
		font-size: 28px;
		margin-top: 10px;
	}
}

.logo_thanks{
	border-radius: 100%;
	width: 200px;
	margin-top: 28px;
	@media all and (min-width: 769px){
		border-radius: 100%;
		margin-top: 50px;
		width: 250px;
		height: auto;
	}
}

.form__textarea--summary {
	width: 90%;
	border: none;
}

.form__newItem {
	display: flex;
	align-self: flex-end;
	margin-bottom: 0px;
	padding-bottom: 0px;
}

.form__newItem:hover {
	background: #ccc;
}

.form__experience--startBlock, .form__experience--endBlock {
	display: flex;
	flex-direction: column;
}

.form__experience--startDate, .form__experience--endDate {
	display: flex;
	flex-direction: row;
	padding: 0;
	margin: 0;
}

.form__experience--currentContainer {
	display: flex;
	flex-direction: column;

	@media all and (min-width: 769px){
		margin-bottom: 20px;
	}
}

.form__experience--currentCheckbox {
	width: 30px;
}
